import React, { memo } from 'react';
import s from './styleProvider';

type PropsT = {
  firstName?: string
  lastName?: string
  role?: string
}

export const Reviewer = memo(({
  firstName,
  lastName,
  role,
}: PropsT) => {
  return (
    <div className={s.body}>
      <span className={s.name}>{firstName} {lastName}</span>
      <span className={s.role}>{role}</span>
    </div>
  );
});
