import React from 'react';
import s from './styleProvider';
import { memo, useContext } from 'react';
import { MiniSiteContext } from 'templatesReact/miniSiteCommon/Context';

const About = memo(() => {

  const { data: {
    name,
    fullName,
    description,
  } } = useContext(MiniSiteContext);

  return (
    <div className={s.body}>
      <h1 className={s.name}>{name}</h1>
      <h2 className={s.fullName}>{fullName}</h2>
      <p className={s.description}>{description}</p>
    </div>
  );
});

export { About };
