import s from './styleProvider';
import React, { memo, useContext } from 'react';
import { Box } from 'templatesReact/miniSiteCommon/box/Box';
import { MiniSiteContext } from 'templatesReact/miniSiteCommon/Context';
import { MapContainer, Marker, Popup, TileLayer } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';
import { Icon } from 'leaflet';

const customIcon = new Icon({
  iconUrl: 'https://freesvg.org/img/Map-Pin.png',
  iconSize: [42, 42]
});

const Location = memo(() => {

  const { data: {
    address,
    name,
    locationLat,
    locationLong,
  } } = useContext(MiniSiteContext);

  if (!locationLat && !locationLong) return null;

  return (
    <Box
      title='Note adresse'
      subTitle={address}
      isBox
      className={s.map}
    >
      <MapContainer
        center={{ lat: locationLat!, lng: locationLong! }}
        zoom={13}
        scrollWheelZoom={false}
        style={{ height: '320px', width: '100%' }}
      >
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        <Marker position={{ lat: locationLat!, lng: locationLong! }} icon={customIcon}>
          <Popup>
            {name}
          </Popup>
        </Marker>
      </MapContainer>
    </Box>
  );
});

export { Location };