import s from './styleProvider';
import React, { memo, useContext, useState } from 'react';
import { MiniSiteContext } from 'templatesReact/miniSiteCommon/Context';
import { CustomButton } from 'components/CustomButton/CustomButton';
import Smartphone from 'assets/svg/Smartphone.svg';
import { PhoneModal } from '../phoneModal';

const Phone = memo(() => {
  const { data: {
    contactNumber,
  } } = useContext(MiniSiteContext);

  const [isOpen, setIsOpen] = useState(false);

  return (
    <div className={s.body}>
      <CustomButton
        text="Show phone number"
        image={Smartphone}
        imagePos="start"
        variant='action'
        width='content'
        onClick={() => setIsOpen(true)}
      />
      <PhoneModal
        isOpen={isOpen}
        onClose={() => setIsOpen(false)}
        onOverlay={() => setIsOpen(false)}
        onEscape={() => setIsOpen(false)}
      />
    </div>
  );
});

export { Phone };
