import React, { memo } from 'react';
import s from './styleProvider';
import EditIcon from 'images/EditIcon.svg';
import { CustomButton } from 'components/CustomButton/CustomButton';
import { Rating } from '../rating';
import { Reviewer } from 'templatesReact/miniSiteCommon/reviewer';

type PropsT = {
  isCard?: boolean;
  padding?: 's' | 'm';
  background?: 'grey' | 'white';
  imageRatio?: 'square' | 'rectangle';

  imageUrl?: string;

  comment?: string;
  date?: Date;

  name?: string;
  description?: string;
  price?: string;

  firstName?: string
  lastName?: string
  email?: string
  role?: string
  rating?: number
  text?: string
  isPosted?: boolean

  isEdit?: boolean;
  editCallback?: () => void;
  removeCallback?: () => void;
}

const CardItem = memo((props: PropsT) => {
  const {
    isCard,
    padding = undefined,
    background = undefined,
    imageRatio = 'square',

    imageUrl,

    comment,
    date,

    name,
    description,
    price,

    firstName,
    lastName,
    email,
    role,
    rating,
    text,
    isPosted,

    isEdit = false,
    editCallback,
    removeCallback,
  } = props;

  const bodyStyle = `${
    s.body
  } ${
    padding && s[padding]
  } ${
    background && s[background]
  } ${
    isCard && s.card
  }`;

  return (
    <div className={bodyStyle}>
      {imageUrl && <div className={`${s.imageContainer} ${s[imageRatio]}`}>
        <img src={imageUrl} className={s.image}/>
      </div>}

      {comment && <div className={s.comment}>{comment}</div>}
      {date && <div className={s.date}>{date.toLocaleString()}</div>}

      {name && <div className={s.name}>{name}</div>}
      {price && <div className={s.price}>{price}</div>}

      {firstName && <Reviewer
        firstName={firstName}
        lastName={lastName}
        role={role}
      />}
      {text && <div className={s.text}>{text}</div>}
      {rating && <Rating rating={rating} />}

      {isEdit && <div className={s.editBlock}>
        <CustomButton
          text='Edit'
          variant='clearBlack'
          size='none'
          image={EditIcon}
          imagePos='start'
          width='content'
          isEdit
          onClick={editCallback}
        />
        <CustomButton
          text='Remove'
          variant='clearBlack'
          size='none'
          image={EditIcon}
          imagePos='start'
          width='content'
          isEdit
          onClick={removeCallback}
        />
      </div>}
    </div>
  );
});

export { CardItem };
