import mscs from './styleCommon/styleProvider';
import React, { memo, ReactNode } from 'react';
import { Box } from './box/Box';
import { CardItem } from './cardItem/CardItem';
import { PhotoItemT } from 'templatesReact/miniSiteCommon/types';

type PropsT = {
  photos: PhotoItemT[]
  isEdit?: boolean;
  titleSize?: 'm' | 'l';
  isBox?: boolean;
  childrenMore?: ReactNode;
  editCallback?: (uuid: string) => void;
  removeCallback?: (uuid: string) => void;
}

const Gallery = memo((props: PropsT) => {
  const {
    photos = [],
    isEdit = false,
    titleSize = 'm',
    isBox = false,
    childrenMore,
    editCallback,
    removeCallback,
  } = props;

  const items = photos.map(item => <CardItem
    key={item.id}
    imageUrl={item.imageUrl}
    comment={item.comment}
    date={item.date}
    isEdit={isEdit}
    editCallback={() => editCallback!(item.id)}
    removeCallback={() => removeCallback!(item.id)}
  />);

  return <Box
    title='Gallery'
    titleSize={titleSize}
    isBox={isBox}
    childrenMore={childrenMore}
  >
    <div className={mscs.gridList}>
      {items}
    </div>
  </Box>;

});

export { Gallery };