import mscs from './styleCommon/styleProvider';
import React, { memo, ReactNode } from 'react';
import { Box } from './box/Box';
import { CardItem } from './cardItem/CardItem';
import { ProductItemT } from 'templatesReact/miniSiteCommon/types';

type PropsT = {
  products?: ProductItemT[]
  isEdit?: boolean
  titleSize?: 'm' | 'l'
  isBox?: boolean
  childrenMore?: ReactNode;
  editCallback?: (uuid: string) => void;
  removeCallback?: (uuid: string) => void;
}

const Shop = memo((props: PropsT) => {
  const {
    products = [],
    isEdit = false,
    titleSize = 'm',
    isBox = false,
    childrenMore,
    editCallback,
    removeCallback,
  } = props;

  const items = products.map(item => <CardItem
    key={item.id}
    imageUrl={item.imageUrl}
    name={item.name}
    description={item.description}
    price={`€${item.price!.toFixed(2)}`}
    isEdit={isEdit}
    editCallback={() => editCallback!(item.id!)}
    removeCallback={() => removeCallback!(item.id!)}
  />);

  return <Box
    title='Shop'
    titleSize={titleSize}
    isBox={isBox}
    childrenMore={childrenMore}
  >
    <div className={mscs.gridList}>
      {items}
    </div>
  </Box>;
});

export { Shop };
