import s from './styleProvider';
import React, { memo, ReactNode } from 'react';

type PropsT = {
  title?: string;
  titleSize?: 'm' | 'l';
  titleGap?: 's' | 'm';
  isBox?: boolean;
  paddingSize?: 's' | 'ms' | 'm';
  background?: 'grey' | 'white';
  subTitle?: string;
  childrenMore?: ReactNode;
  className?: string;
  children?: ReactNode;
}

const Box = memo((props: PropsT) => {
  const {
    title,
    titleSize = 'm',
    titleGap = 'm',
    subTitle,
    isBox = false,
    paddingSize = 'm',
    background = 'white',
    childrenMore,
    className,
    children,
  } = props;

  const s_body = `${
    s.body
  } ${
    isBox && s.body_box
  } ${
    isBox && s[`box_${paddingSize}`]
  } ${
    isBox && s[`box_${background}`]
  }`;
  const s_title = `${s.title} ${s[`title_${titleSize}`]}`;

  return (
    <div className={s_body}>
      {(title || subTitle) && <div className={`${s.titleContainer} ${children && s[`title_gap_${titleGap}`]}`}>
        <div className={s.titleTextContainer}>
          {title && <h3 className={s_title}>{title}</h3>}
          {subTitle && <h4 className={s.subTitle}>{subTitle}</h4>}
        </div>
        {childrenMore}
      </div>}
      {children && <div className={className}>
        {children}
      </div>}
    </div>
  );
});

export { Box };
