import s from './styleProvider';
import React, { memo, useContext, useState } from 'react';
import { CustomButton } from 'components/CustomButton/CustomButton';
import { MiniSiteContext } from 'templatesReact/miniSiteCommon/Context';
//import { CustomSelectFile } from 'components/CustomInput/CustomSelectFile';

type PropsT = {
  onClose?: () => void
}

const PhoneModalContent = memo((props: PropsT) => {
  const {
    onClose,
  } = props;

  const { data, editImages } = useContext(MiniSiteContext);

  const [currentImage, setCurrentImage] = useState(data.imageUrl);
  const [currentLogo, setCurrentLogo] = useState(data.logoUrl);

  const submit = () => {
    editImages(
      currentImage,
      currentLogo,
    );
    onClose!();
  };

  return (
    <div className={s.body}>
      <p className={s.description}>
        If you’re a professional, upgrade to premium to show your phone
      </p>
      <div className={s.buttonContainer}>
        <CustomButton
          text="Cancel"
          variant='outline'
          size='l'
          width='standard'
          onClick={onClose}
        />
        <CustomButton
          text="Save changes"
          variant='action'
          size='l'
          width='standard'
          onClick={submit}
        />
      </div>
    </div>
  );
});

export { PhoneModalContent };
