import React, { memo } from 'react';
import { Modal, ModalPublicProps } from 'components/modal/Modal';
import { useToggle } from 'hooks/useToggle';
import { PhoneModalContent } from './PhoneModalContent';


const PhoneModal = memo((props: ModalPublicProps) => {
  const {
    className,
    isOpen,
    onClose,
    onOverlay,
    onEscape,
  } = props;

  const [triggerClosing, onTriggerClosing] = useToggle(false);

  return (
    <Modal
      className={className}
      title={'The professional didn’t share his phone'}
      widthSize='s'
      isOpen={isOpen}
      onClose={onClose}
      onOverlay={onOverlay}
      onEscape={onEscape}
      triggerClosing={triggerClosing}
      onTriggerClosing={onTriggerClosing}
    >
      <PhoneModalContent onClose={onTriggerClosing}/>
    </Modal>
  );
});

export { PhoneModal };