import s from './styleProvider';
import React, { FunctionComponent, memo, SVGAttributes } from 'react';
import ContactInstagram from 'assets/svg/ContactInstagram.svg';
import ContactFacebook from 'assets/svg/ContactFacebook.svg';
import ContactX from 'assets/svg/ContactX.svg';
import ContactWeb from 'assets/svg/ContactWeb.svg';
import { ContactItemT } from 'templatesReact/miniSiteCommon/types';

const iconDict: Record<string, FunctionComponent<SVGAttributes<SVGAElement>>> = {
  'instagram': ContactInstagram,
  'facebook': ContactFacebook,
  'x': ContactX,
  'web': ContactWeb,
};

type PropsT = {
  contacts: ContactItemT[]
}

const Contacts = memo(({
  contacts
}: PropsT) => {

  const icons = contacts.map(item => {
    const Icon = iconDict[item.type];
    return (
      <div key={item.id} className={s.iconContainer}>
        <Icon/>
      </div>
    );
  });

  return (
    <div className={s.body}>
      {icons}
    </div>
  );
});

export { Contacts };
