import React, { memo, useCallback } from 'react';
import Star from 'assets/svg/star.svg';
import s from '../styleCommon/styleProvider';

type PropsT = {
  rating: number
  onChange?: (val: number) => void
}

const Rating = memo((props: PropsT) => {
  const {
    rating,
    onChange,
  } = props;

  const clickHandler = useCallback((value: number) => {
    onChange && onChange(value);
  }, []);

  const stars = Array.from({ length: 5 }, (_, index) => {
    return <Star
      key={index}
      className={`${index < rating ? s.starFilled : s.starEmpty}`}
      onClick={() => clickHandler(index)}
    />;
  });

  return <div className={s.starContainer}>{stars}</div>;
});

export { Rating };
