import s from './styleProvider';
import React, { memo, ReactNode } from 'react';
import { Box } from '../../miniSiteCommon/box/Box';
import { CardItem } from '../../miniSiteCommon/cardItem/CardItem';
import { ReviewItemT } from 'templatesReact/miniSiteCommon/types';

type PropsT = {
  reviews: ReviewItemT[];
  isBox?: boolean
  childrenMore?: ReactNode;
}

const Review = memo((props: PropsT) => {
  const {
    reviews,
    isBox = true,
    childrenMore,
  } = props;

  const items = reviews.map(item => <CardItem
    key={item.id}
    isCard
    padding='m'
    background='grey'
    firstName={item.firstName}
    lastName={item.lastName}
    email={item.email}
    role={item.role}
    rating={item.rating}
    text={item.text}
    isPosted={item.isPosted}
  />);

  return <Box
    title='Review'
    isBox={isBox}
    childrenMore={childrenMore}
    className={s.list}
  >
    {items}
  </Box>;
  
});

export { Review };
